#home {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    #main_content {
        width: 100%;
        height: 75%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        #text_container {
            // border: 1px solid blue;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;

            .title {
                display: block;

                h2 {
                    font-size: 60px;
                    letter-spacing: 1px;
                    line-height: 3vh;

                }

                h3 {
                    font-size: 40px;
                    line-height: 3vh;
                    margin-block-end: 0.2em;
                }
            }

            .paragraph {
                height: 35%;
                margin-top: 3vh;
                font-size: 3vh;

                span {
                    border-bottom: 2px solid rgb(255, 231, 14);
                }
            }
        }



        #img_container {
            padding: 0 5vh;

            img {
                box-shadow: 4px 3px 5px rgb(70, 69, 69);
                width: 25vh;
                border-radius: 20px;
            }
        }
    }




}